.message{
    text-align: center;
    padding: 0.7em;
    border: 1px solid black;
    border-radius: 3px; 
    margin-bottom: 1.5em;
}
.success{
    background-color: rgba(0, 128, 0, 0.432);
    color: white;
}
.failed{
    background-color: rgba(128, 0, 0, 0.432);
    color: white;
}